import React, { useState, useEffect } from "react";
import ReportTableAllTasks from "./ReportTableAllTasks/ReportTableAllTasks";
import ReportTableBody from "./ReportTableBody/ReportTableBody";

import { useStoreState, useStoreActions } from "easy-peasy";

function ReportTable({
  tasks,
  suid,
  activityid,
  setTasksVisible,
  isTaskLoading
}) {
  let { user_id } = useStoreState(state => state.profile);
  let { fname, lname } = useStoreState(state => state.profile);
  let fetchActivityTask = useStoreActions(
    action => action.admin.fetchActivityTask
  );

  let setTasks = useStoreActions(action => action.admin.setTasks); // admin casuse remnants of the past

  let [currentUserId, setCurrentUserId] = useState(user_id); // to set and view all the tasks of the given person
  let [isUserTasksVisible, setUserTasksVisible] = useState(false);

  let [newTasks, setNTask] = useState([]); // set new tasks

  let [dependency, setDependency] = useState("");
  let [category, setCategory] = useState("");
  let [tdesc, setTDesc] = useState("");

  useEffect(() => {
    if (isTaskLoading) fetchActivityTask(activityid);
    // if new tasks are to be loaded
    setIsTasksAssignable(evaluateTaskAssignable(tasks, newTasks));
    // evaluate if new tasks are assignable for this person
  }, [
    isTaskLoading,
    evaluateTaskAssignable,
    fetchActivityTask,
    activityid,
    tasks,
    newTasks
  ]);

  function evaluateTaskAssignable(tasks, newTasks) {
    return (
      (!isTaskLoading &&
        tasks &&
        tasks.every(task => task.user_id !== user_id)) ||
      (newTasks &&
        newTasks.length &&
        newTasks.every(task => task.user_id !== user_id))
    );
  }

  let [isTaskAssignable, setIsTasksAssignable] = useState(false);

  function setCorrectTask() {
    setNTask([
      {
        user_id: user_id,
        name: fname + " " + lname,
        task_description: tdesc,
        dependency,
        category
      }
    ]);
  }

  const isAnyNewTasks = () => {
    return newTasks && newTasks.length;
  };

  const isAnyTasks = () => {
    return (tasks && tasks.length) || (newTasks && newTasks.length);
  };

  const TableButtons = ({ isTaskAssignable, isAnyNewTasks }) => (
    <>
      {isTaskAssignable && !isAnyNewTasks ? (
        <button
          className="blk btn  mr-16t mr-12r"
          onClick={e => {
            setCorrectTask();
            setIsTasksAssignable(false);
          }}
        >
          Submit Changes
        </button>
      ) : null}
      {isAnyNewTasks ? (
        <>
          <button
            onClick={e => {
              setNTask([]);
              setIsTasksAssignable(true);
            }}
            className="btn mr-16"
          >
            Cancel
          </button>
          <button
            className="blk btn  mr-16t mr-12r"
            onClick={e => {
              setTasksVisible(false);
              setNTask([]);
              setIsTasksAssignable(false);
              setTasks({ user_id: newTasks, milestone: "", activityid, suid });
            }}
          >
            Submit report
          </button>
        </>
      ) : null}
    </>
  );

  return (
    <>
      <table id="task-table" className="br-collapse anim-down">
        <ReportTableBody
          tdesc={tdesc}
          setTDesc={setTDesc}
          name={fname + " " + lname}
          dependency={dependency}
          setDependency={setDependency}
          category={category}
          setCategory={setCategory}
          setUserTasksVisible={setUserTasksVisible}
          setCurrentUserId={setCurrentUserId}
          setTasksVisible={setTasksVisible}
          tasks={tasks}
          isHeaderVisible={isAnyTasks() || !isAnyNewTasks()}
          isRowsAvailable={isAnyTasks() && !isTaskLoading}
          isRowsLoading={isTaskLoading}
          isRowUpdatable={isTaskAssignable}
          rows={[...tasks, ...newTasks]}
          fname={fname}
          lname={lname}
        />
      </table>
      <TableButtons
        isAnyNewTasks={isAnyNewTasks()}
        isTaskAssignable={isTaskAssignable}
      />
      {isUserTasksVisible ? (
        <ReportTableAllTasks
          setUserTasksVisible={setUserTasksVisible}
          userId={currentUserId}
          activityId={activityid}
        />
      ) : null}
    </>
  );
}

export default ReportTable;
