import React, { useEffect } from "react";
import { useStoreState, useStoreActions } from "easy-peasy";

function ReportQuery({ setVisible }) {
  let users = useStoreState(state => state.auth.users);
  let getAllUsers = useStoreActions(action => action.auth.getAllUsers);
  useEffect(() => {
    if (!users) getAllUsers();
  }, [getAllUsers, users]);
  return (
    <>
      <section className="mr-16x pd-16x disp-flex fl-jc-fe">
        {users ? (
          <button
            className="blk btn mr-4r"
            onClick={e => {
              setVisible(true);
            }}
          >
            Make a new team
          </button>
        ) : (
          <p>Loading users</p>
        )}
        {/* <button class="blk btn mr-4r">Submit search</button> */}
      </section>
    </>
  );
}

export default ReportQuery;
